<template>
  <form @submit.prevent="submitForm">
    <div class="form-controls" :class="{ invalid: !firstName.isValid }">
      <label for="firstname">FirstName</label>
      <input
        type="text"
        id="firstname"
        v-model.trim="firstName.val"
        @blur="clearValidity('firstName')"
      />
      <p v-if="!firstName.isValid">FirstName must not be empty</p>
    </div>

    <div class="form-controls" :class="{ invalid: !lastName.isValid }">
      <label for="lastname">LastName</label>
      <input
        type="text"
        id="lastname"
        v-model.trim="lastName.val"
        @blur="clearValidity('lastName')"
      />
      <p v-if="!lastName.isValid">LastName must not be empty</p>
    </div>

    <div class="form-controls" :class="{ invalid: !description.isValid }">
      <label for="description">Description</label>
      <textarea
        id="description"
        rows="5"
        v-model.trim="description.val"
        @blur="clearValidity('description')"
      ></textarea>
      <p v-if="!description.isValid">Description must not be empty</p>
    </div>

    <div class="form-controls" :class="{ invalid: !stars.isValid }">
      <h3>Stars of the Merchandise</h3>
      <Rate v-model="stars.val" />
    </div>
    <p v-if="!formIsValid">Please fix the above errors and submit again</p>
    <base-button>Submit</base-button>
  </form>
</template>

<script>
export default {
  data() {
    return {
      firstName: {
        val: "",
        isValid: true
      },
      lastName: {
        val: "",
        isValid: true
      },
      description: {
        val: "",
        isValid: true
      },
      stars: {
        val: null,
        isValid: true
      },
      formIsValid: true
    };
  },
  methods: {
    clearValidity(input) {
      //   console.log(input);
      this[input].isValid = true;
    },
    validateForm() {
      this.formIsValid = true;
      if (this.firstName.val === "") {
        this.firstName.isValid = false;
        this.formIsValid = false;
      }
      if (this.lastName.val === "") {
        this.lastName.isValid = false;
        this.formIsValid = false;
      }
      if (this.description.val === "") {
        this.description.isValid = false;
        this.formIsValid = false;
      }
      if (this.stars.val.length === 0) {
        this.stars.isValid = false;
        this.formIsValid = false;
      }
    },
    submitForm() {
      this.validateForm();
      if (!this.formIsValid) {
        return;
      }
      const formData = {
        first: this.firstName.val,
        last: this.lastName.val,
        desc: this.description.val,
        stars: this.stars.val
      };
      console.log(formData);
      this.$emit("save-data", formData);
      this.firstName.val = "";
      this.lastName.val = "";
      this.description.val = "";
      this.stars.val = "";
    }
  }
};
</script>

